@import "variables";
@import "mixins";

/*
* Text-style
* ================ */
.big {
	font-size: 18px;
}

.font {
	&__family {
		&-open-sans {
			font-family: 'Open Sans', sans-serif;
		}
		&-montserrat {
			font-family: 'Montserrat', sans-serif;
		}
		&-montserrat-alt {
			font-family: 'Montserrat Alternates', sans-serif;
		}
		&-playfair {
			font-family: 'Playfair Display', sans-serif;
		}
		&-poppins {
			font-family: 'Poppins', sans-serif;
		}
		&-pacifico {
			font-family: 'Pacifico', sans-serif;
		}
		&-roboto {
			font-family: 'Roboto', sans-serif;
		}
		&-roboto-slab {
			font-family: 'Roboto Slab', sans-serif;
		}
		&-oxygen {
			font-family: 'Oxygen', sans-serif;
		}
		&-times-new-roman {
			font-family: 'Times New Roman', sans-serif;
		}
	}
	&__weight {
		&-thin {
			font-weight: 100;
		}
		&-ultralight {
			font-weight: 200;
		}
		&-light {
			font-weight: 300;
		}
		&-normal {
			font-weight: 400;
		}
		&-medium {
			font-weight: 500;
		}
		&-semibold {
			font-weight: 600;
		}
		&-bold {
			font-weight: 700;
		}
		&-extrabold {
			font-weight: 800;
		}
	}
	&__style {
		&-italic {
			font-style: italic;
		}
		&-normal {
			font-style: normal;
		}
	}
}

@each $breakpoint in map-keys($brk-grid-breakpoints) {
	@include media-breakpoint-up($breakpoint) {
		$infix: breakpoint-infix($breakpoint, $brk-grid-breakpoints);

		@for $i from 10 through 86 {
			.font__size#{$infix}-#{$i} {font-size: rem($i)}
		}

		@each $fs in $font-size {
			.font__size#{$infix}-#{$fs} {font-size: rem($fs)}
		}

	}
}

// Text weight
//
.font-weight-normal {
	font-weight: 400 !important;
}

.font-weight-bold {
	font-weight: 700;
}

.font-weight-light {
	font-weight: 300;
}

.font-weight-thin {
	font-weight: 100;
}

// Text spacing
//
.letter-spacing {
	&--100 {
		letter-spacing: -1px;
	}
	&-1{
		letter-spacing: 0.01em
	}
	&--1{
		letter-spacing: -0.01em
	}
	&-20 {
		letter-spacing: 0.02em !important;
	}
	&--20{
		letter-spacing: -0.02em;
	}
	&-40 {
		letter-spacing: 0.04em;
	}
	&--40 {
		letter-spacing: -0.04em;
	}
	&-60 {
		letter-spacing: 0.06em;
	}
	&-100 {
		letter-spacing: 0.1em;
	}
	&-140 {
		letter-spacing: 0.14em;
	}
	&-200 {
		letter-spacing: 0.2em;
	}
	&-300 {
		letter-spacing: 0.3em;
	}
}

